import { Component } from '@angular/core';
import { CameraPreview, CameraPreviewPictureOptions, CameraPreviewOptions, CameraPreviewDimensions } from '@ionic-native/camera-preview/ngx';


@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  record: boolean = false;
  // camera options (Size and location). In the following example, the preview uses the rear camera and display the preview in the back of the webview

  constructor(public cameraPreview: CameraPreview) {

  }

  startCam() {
    let cameraPreviewOpts = {
      x: 3*window.screen.width/4,
      y: 3*window.screen.height/4,
      width: window.screen.width/4,
      height: window.screen.height/4,
      camera: this.cameraPreview.CAMERA_DIRECTION.FRONT,
      toBack: false,
      tapPhoto: true,
      tapFocus: false,
      previewDrag: false,
      storeToFile: false,
      disableExifHeaderStripping: false
    };

    if (!this.record) {
      this.cameraPreview.startCamera(cameraPreviewOpts).then(
        (res) => {
          this.record = true;
          console.log("OKK");
          console.log(res);
        },
        (err) => {
          console.log("KOO");
          console.log(err);
        });
    } else {
      this.record = false;
      this.cameraPreview.stopCamera();
    }

  }

  switchCam() {
    this.cameraPreview.switchCamera();
  }


}
